<template>
<div v-resize="onResize">
  <div class="ma-2" v-show="mobile == false">
    <v-row dense>
      <v-col cols="6">
        <v-sheet class="rounded-lg" outlined>
          <v-btn block class="rounded-lg" depressed :color="btnmode ? 'red darken-2 white--text' : ''" @click="toggleTheme(true)">
            <span class="text-capitalize subtitle-1">Gelap</span>
            <v-icon right :color="btnmode ? '' : ''">mdi-weather-night</v-icon>
          </v-btn>
        </v-sheet>
      </v-col>
      <v-col cols="6">
        <v-sheet class="rounded-lg" outlined>
          <v-btn dark block class="rounded-lg" depressed :color="btnmode ? '' : 'red darken-2'" @click="toggleTheme(false)">
            <span class="text-capitalize subtitle-1">Terang</span>
            <v-icon right :color="btnmode ? '' : ''">mdi-white-balance-sunny</v-icon>
          </v-btn>
        </v-sheet>
      </v-col>
    </v-row>
  </div>

  <v-list-item v-show="mobile == true" @change="toggleTheme2()">
    <v-list-item-avatar>
      <v-icon v-if="!$vuetify.theme.dark" :color="$vuetify.theme.dark ? '' : 'grey darken-4'">mdi-white-balance-sunny</v-icon>
      <v-icon v-else :color="$vuetify.theme.dark ? '' : 'grey darken-4'">mdi-weather-night</v-icon>
    </v-list-item-avatar>

    <v-list-item-content>
      <v-list-item-title class="subtitle-1"><strong>Mode Malam</strong></v-list-item-title>
      <v-list-item-subtitle v-if="!$vuetify.theme.dark">Tidak Aktif</v-list-item-subtitle>
      <v-list-item-subtitle v-else>Aktif</v-list-item-subtitle>
    </v-list-item-content>
    <v-list-item-action>
      <v-switch
        @change="toggleTheme2()"
        v-model="$vuetify.theme.dark"
        hide-details
        color="error"
      ></v-switch>
    </v-list-item-action>
  </v-list-item>
</div>
</template>
<script>
import Vue from 'vue'
import Vuetify from 'vuetify/lib'

Vue.use(Vuetify)

export default {
  data: () => ({
    btnmode:null,
    mobile:null,
    windowSize: {x: 0, y: 0},
    // switchModeMalam: "",
  }),
  computed: {
    // scrollbarTheme() {
    //   return this.$vuetify.theme.dark ? 'dark' : 'light';
    // },
  },

  watch: {
    windowSize(){
      if (this.windowSize.x < 500) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },
  },
  
  methods: {
    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
    toggleTheme2: function () {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      // this.switchModeMalam = !this.$vuetify.theme.dark;
      localStorage.setItem("darktheme", this.$vuetify.theme.dark.toString());
      console.log(this.$vuetify.theme.dark);
    },
    toggleTheme(e){
       this.$vuetify.theme.dark = e ? true : false;
        // this.switchModeMalam = !this.$vuetify.theme.dark;
        localStorage.setItem("darktheme", this.$vuetify.theme.dark.toString());
        console.log(this.$vuetify.theme.dark);
        this.btnmode = this.$vuetify.theme.dark
    }
  },

  mounted() {
    const theme = localStorage.getItem("darktheme");
    if (theme) {
      if (theme == "true") {
        this.$vuetify.theme.dark = true;
        this.switchModeMalam = this.$vuetify.theme.dark;
        this.btnmode = true
      } else {
        this.$vuetify.theme.dark = false;
        this.switchModeMalam = this.$vuetify.theme.dark;
        this.btnmode = false
      }
    }
  },
};
</script>