<template>
  <v-app>
    <v-main class="NoneTransition" :class="$vuetify.theme.dark ? 'black' : 'grey lighten-4'">
      <router-view v-if="active"/>
    </v-main>
    <Appbar v-if="isblock"/>
  </v-app>
</template>

<script>
  import Vue from 'vue'
  import CryptoJS from 'crypto-js';
  import Vuetify from 'vuetify/lib'
  import Appbar from '@/views/Appbar';
  import PouchDB from 'pouchdb';

  Vue.use(Vuetify)
  // const Appbar = () => import (/* webpackChunkName: "appbar"*/ '@/views/Appbar.vue')
  export default {
    components: {
      Appbar,
    },

    data () {
      return {
        keycrypt:"JfJbppnWXj3ASS5RSTROTQlbHGpfJIvjhKv7JiHMOgA=",
        isblock:false,
        device:'',
        active:true,
        errlokasi:null,
        rute:null,
        ruteblock:['SuratCuti','AbsenQr','CameraQr']
        // switchModeMalam: ""
      }
    },
    beforeCreate(){
      var db = new PouchDB('dboffline');
      db.info().then(function (info) {
        if(info){console.log(`DB OFF OK`)}
      })
      Vue.prototype.$db = db
      Vue.prototype.$conn = null
      if (navigator.onLine) {
          this.$conn = 'online'
          sessionStorage.setItem('Koneksi',this.$conn)
        } else {
          this.$conn = 'offline'
          sessionStorage.setItem('Koneksi',this.$conn)
          console.log(this.$conn)
        }
      window.addEventListener('offline', () => {
          this.$conn = 'offline'
          this.ToastErrKoneksi('Kamu Di Mode '+this.$conn)
          sessionStorage.setItem('Koneksi',this.$conn)
      });
      window.addEventListener('online', () => {
         this.$conn = 'online'
         this.ToastErrKoneksi('Anda Kembali '+this.$conn)
         sessionStorage.setItem('Koneksi',this.$conn)
      });
      // this.ToastErrKoneksi(this.$conn)
      function cekcamera(){
      navigator.mediaDevices.getUserMedia({video:true}).then(c =>{
          c.getTracks().forEach(x =>{
             x.stop()
          });
          localStorage.setItem('Cam','true')
        }).catch(err => {
          if(err)
            localStorage.setItem('Cam','false')
        })
      }
      cekcamera()
    },
    mounted(){
      let cek = navigator.userAgent.split(/\s*[;)(]\s*/);
      if(cek[1] == "Linux" || cek[1] == "iPhone" || cek[1] == "iPad"){
        this.device = "Mobile"
      }else{
        this.device = "Desktop"
      }
      this.lokasicek()
      this.cameracek()
      let camactive = localStorage.getItem('Cam')
      Vue.prototype.$encrypt = this.encrypt
      Vue.prototype.$decrypt = this.decrypt
      if(camactive == 'false' && this.device == 'Mobile'){
        this.ToastErr('Anda Disable GPS Lokasi')
      }
      Vue.prototype.$lokasierr = this.errlokasi
      let socket = new WebSocket("ws://127.0.0.1:6001");

        socket.onopen = function(e) {
          console.log(e)
          console.log("[open] Connection established");
          console.log("Sending to server");
          socket.send("My name is John");
        };
      // this.active = this.active == false ? this.errlokasi == null : true
    },
    methods:{
      cameracek(){
        if(this.device == 'Mobile'){
          navigator.mediaDevices.getUserMedia({video:true}).then(c =>{
          c.getTracks().forEach(x =>{
             x.stop()
          });
          localStorage.setItem('Cam','true')
        }).catch(err => {
          if(err)
            localStorage.setItem('Cam','false')
            this.ToastErr('Anda Disable Camera')
        })
        }
      },
        lokasicek(){
          var options = {
            enableHighAccuracy: true,
            timeout: 10000,
            maximumAge: 0
          };
              if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(this.showPosition, this.showError, options);
              } else { 
                this.errlokasi = "Geolocation is not supported by this browser."
                console.log("Geolocation is not supported by this browser.");
              }
            },

            showPosition(position) {
              return position
            },

            showError(error) {
              switch(error.code) {
                case error.PERMISSION_DENIED:
                  // console.log("User denied the request for Geolocation.")
                  this.errlokasi = "Anda Disable GPS Lokasi."
                  if(this.device == 'Mobile'){
                    this.ToastErr(this.errlokasi)
                  }
                  break;
                case error.POSITION_UNAVAILABLE:
                  this.errlokasi = "Informasi Lokasi Tidak Tersedia."
                  if(this.device == 'Mobile'){
                    this.ToastErr(this.errlokasi)
                  }
                  break;
                case error.TIMEOUT:
                  // console.log("The request to get user location timed out.")
                  this.errlokasi = "Kehabisan Waktu Ketika Mengambil Lokasi (Masalah Jaringan)."
                  if(this.device == 'Mobile'){
                    this.ToastErr(this.errlokasi)
                  }
                  break;
                case error.UNKNOWN_ERROR:
                  // console.log("An unknown error occurred.")
                  this.errlokasi = "Error yang tidak diketahui."
                  if(this.device == 'Mobile'){
                    this.ToastErr(this.errlokasi)
                  }
                  break;
              }
            },
        encrypt(data){
        var CryptoJSAesJson = {
              stringify: function (cipherParams) {
                  var j = {ct: cipherParams.ciphertext.toString(CryptoJS.enc.Base64)};
                  if (cipherParams.iv) j.iv = cipherParams.iv.toString();
                  if (cipherParams.salt) j.s = cipherParams.salt.toString();
                  return JSON.stringify(j);
              },
              parse: function (jsonStr) {
                  var j = JSON.parse(jsonStr);
                  var cipherParams = CryptoJS.lib.CipherParams.create({ciphertext: CryptoJS.enc.Base64.parse(j.ct)});
                  if (j.iv) cipherParams.iv = CryptoJS.enc.Hex.parse(j.iv)
                  if (j.s) cipherParams.salt = CryptoJS.enc.Hex.parse(j.s)
                  return cipherParams;
              }
          }
          return CryptoJS.AES.encrypt(JSON.stringify(data), this.keycrypt, {format: CryptoJSAesJson}).toString();
      },
      decrypt(data){
        var CryptoJSAesJson = {
              stringify: function (cipherParams) {
                  var j = {ct: cipherParams.ciphertext.toString(CryptoJS.enc.Base64)};
                  if (cipherParams.iv) j.iv = cipherParams.iv.toString();
                  if (cipherParams.salt) j.s = cipherParams.salt.toString();
                  return JSON.stringify(j);
              },
              parse: function (jsonStr) {
                  var j = JSON.parse(jsonStr);
                  var cipherParams = CryptoJS.lib.CipherParams.create({ciphertext: CryptoJS.enc.Base64.parse(j.ct)});
                  if (j.iv) cipherParams.iv = CryptoJS.enc.Hex.parse(j.iv)
                  if (j.s) cipherParams.salt = CryptoJS.enc.Hex.parse(j.s)
                  return cipherParams;
              }
          }
          return JSON.parse(CryptoJS.AES.decrypt(JSON.stringify(data), this.keycrypt, {format: CryptoJSAesJson}).toString(CryptoJS.enc.Utf8));
      },
      ToastErrKoneksi(data){
        this.$swal.fire({
        toast: true,
        icon: this.$conn == 'online' ? 'success' : 'error',
        title: data,
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        position: 'top-right'})
      },
      ToastErr(data){
        this.$swal.fire({
        icon: 'error',
        title: data,
        position: 'top-center'}).then(x => {
          if (x) {
            // console.log(this.errlokasi)
            this.lokasicek()
            this.cameracek()
            // console.log(this.errlokasi)
            // this.reload()
          }
        })  
      },
      reload(){
        if(this.errlokasi == null){
          location.reload()
        }
      }
    },
    watch:{
      $route(){
        // console.log('eyy',this.$route)
        this.rute = this.$route.name
        let check = this.ruteblock.includes(this.$route.name)
        if(check == true){
          this.isblock = false
        }else{
          this.isblock = true
        }
      }
    },
    
  };
</script>

