                    <v-row
                      class="fill-height ma-0"
                      align="center"
                      justify="center"
                    >
                      <v-icon large :color="$vuetify.theme.dark ? '' : 'grey darken-4'">mdi-account</v-icon>
                    </v-row>
                  </template>
