          <v-img @click="home" class="ml-2 mr-2" max-width="30" src="../assets/LogoTrinity.png" style="cursor: pointer;"></v-img>

          <v-toolbar-title @click="home">
            <router-link to="home" tag="span" exact style="cursor: pointer;">
