              <div class="caption" v-text="appdata.NamaAlias.Nilai"></div>
            </router-link>
          </v-toolbar-title>
        </v-toolbar>
      </template>

      <v-list>
        <!-- Single Item -->
        <v-list-item-group>
          <v-list-item
            v-for="(item,key) in Beranda"
